*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background: cornflowerblue;
}
.main_div{
  width: 100%;
  height: 100vh;
  background: cornflowerblue;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  width: 100%;
  height: fit-content;
  background-color: rgb(216, 157, 216);
  box-shadow: 5px 5x 25px -5px rgb(92, 54, 54);
  border-radius: 15px;
}
.control_div{
  width: 25%;
  height: 80vh;
  background-color: rgb(216, 157, 216);
  box-shadow: 5px 5x 25px -5px rgb(92, 54, 54);
  border-radius: 15px;
}


h1{
  color: white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5x 25px -5px rgb(92, 54, 54);
}
input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
}
button{
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #8566aa;
  color: #fff;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5x 25px -5px rgb(0, 0, 0,0.3);

}
button:hover{
  background-color: #20bf60;
}
ol{
  margin-top: 30px;
}
ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
  background-color: rgb(216, 157, 216);
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  background-color: rgb(216, 157, 216);
  
}

.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5x 25px -5px rgb(0, 0, 0,0.3);
}
.todo_style .fa-times:hover{
  background-color: crimson;
}